body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  box-sizing: border-box;
}
header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #a9adac;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

header a {
  color: black;
  text-decoration: none;
}

.header-name {
  color: #e27a3f;
}

.header-name {
  font-size: 1rem;
  text-align: center;
  color: #e27a3f;
}

@media screen and (min-width: 800px) {
  .header-name {  
    font-size: 2rem;
  }

  header .logo-container {
    margin-left: 20px;
  }
}

.button-link {
  text-decoration: none;
}

.logo-container {
  margin: 0;
  height: 90%;
  display: flex;
}

@media screen and (min-width: 800px) {
  .hamburger, .hamburger--collapse {
    display: none !important;
  }
}
.login-button {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin: 5px 10px 5px 0;
  }

.login-button button {
    margin: 0px 5px;
    padding: 10px 21px;
    background: #e27a3f;
    color: rgba(255,255,255, 0.75);
    font-size: 1rem;
    cursor: pointer;
    -webkit-transition: color 0.2s ease-out;
    transition: color 0.2s ease-out;
    outline: 0;
    border: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.login-button button:hover {
    color: white;
}
