.main {
  display: flex;
}
.navigation {
  flex-basis: 20%;
  min-width: 200px;
  padding: 20px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  position: fixed;
  left: -340px;
}

@media screen and (min-width: 800px) {
  .navigation {
    left: 0;
    position: relative;
  }
}

.navigation a {
  position: fixed;
  bottom: 8px;
  justify-content: center;
  color: #e27a3f;
  text-decoration: none;
}
.selection {
  display: flex;
  flex-direction: column;
  margin-bottom: 60px;
  padding-top: 20px;
}

.selection label {
  font-size: 1rem;
  margin-bottom: 10px;
  color: #555;
}

.selection select {
  max-width: 150px;
}
.slider {
  margin-bottom: 40px;
}

.slider label {
  color: #555;
  font-size: 1rem;
  margin-bottom: 20px;
  display: block;
  text-transform: capitalize;
}

.input-range__slider {
  background: #e27a3f;
  border: none;
}

.input-range__track {
  background: #ddd;
}

.input-range__track--active {
  background: #e27a3f;
}

.input-range__label--value .input-range__label-container {
  background: #e27a3f;
  color: white;
  font-size: 0.65rem;
  padding: 2px 5px;
  border-radius: 2px;
}

.input-range__label--min .input-range__label-container,
.input-range__label--max .input-range__label-container
 {
  font-size: 0.65rem;
  color: #888;
  left: 0;
}

.input-range__label--max .input-range__label-container {
  left: 25%;
}

.search-button {
  display: flex;
  justify-content: center;
}
.search-button button {
  padding: 10px 21px;
  background: #e27a3f;
  color: rgba(255,255,255, 0.75);
  font-size: 1rem;
  cursor: pointer;
  -webkit-transition: color 0.2s ease-out;
  transition: color 0.2s ease-out;
  outline: 0;
  border: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.search-button button:hover {
  color: white;
}
.search-field {
    display: flex;
    justify-content: center;
    padding-bottom: 20px;
    border-bottom: 2px solid #e27a3f;
    width: 100%;
}

.search-field button {
    padding: 5px 15px;
    background: #e27a3f;
    color: rgba(255,255,255, 0.75);
    cursor: pointer;
    -webkit-transition: color 0.2s ease-out;
    transition: color 0.2s ease-out;
    outline: 0;
    border: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    color: white;
    border-radius: 0 3px 3px 0;
}

@media screen and (min-width: 800px) {
    .search-field button {
        padding: 5px 8px;
    }
}

.search-box {
    border-radius: 3px 0 0 3px;
    padding-left: 5px;
    width: 80%;
}

/* 1440p support */
@media screen and (min-height: 1100px) {
    .search-box {
      font-size: 1.5em;
    }
}
  /* 4k support */
    @media screen and (min-height: 1500px) {
    .search-box {
      font-size: 2em;
    }
}
  

.search-box:focus {
    border-color: #e27a3f;
    outline: 0;
}
